@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding:0;
  margin:0;
}

a{
  text-decoration: none;
}


body {
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color:white;
  max-width: 1100px;
  margin: 0 auto;
}

a{
  color: lightgray;
}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}.won-card{background-color:#4da375}.loss-card{background-color:#a34d5d}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name{font-size:4rem;margin-bottom:20px}.TeamPage .win-loss-section{justify-self:center;padding:40px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{align-self:center;justify-self:center;font-size:1.5rem}

.WinLossChart{padding:20px;text-align:center}.WinLossChart .win-loss-section{justify-self:center;padding:20px}

.HomeIcon{height:100px;text-align:left;width:200px;font-size:10px;padding:20px}

.MatchPage{display:grid;grid-template-columns:1fr 8fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:end}.MatchPage .page-heading{margin:20px auto}.MatchPage .no-matches-found{text-align:center}

.YearSelector{text-align:left;list-style-type:none}.YearSelector li{margin-bottom:10px;margin-top:10px}

.VSTeamSelector{text-align:left;list-style-type:none}.VSTeamSelector li{margin-bottom:10px;margin-top:10px;font-size:13px}

.VSMatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px}.VSMatchPage .team-selector{justify-self:end}.VSMatchPage .page-heading{margin:20px auto}.VSMatchPage .no-matches-found{text-align:center}

.TeamTile{height:170px;text-align:center;border:1px solid white;padding:20px}

.HomePage .app-name{font-size:3rem;margin:30px auto;text-align:center}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}

.App{text-align:left;padding:20px}

